import React, { useState } from 'react';
import { useHistory, useLocation, Link } from "react-router-dom";
import { connect } from "react-redux";
import { actions } from "../redux/action";
import logoApp from "../assets/img/xd/לוגו איקומרס-01.png"
import { Col, Container, Row } from 'reactstrap';
import logoFurnitureShop from '../assets/media-gallery/modern-luxury-living-room-interior-black-sofa-with-dark-concrete-wall.png'
import cloudGrey from "../assets/img/xd/cloud grey.png"
import reka from "../assets/img/xd/רקע.png"
import "./onBoarding.css"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


function OnBoarding(props) {
        const history = useHistory();
        const location = useLocation();

        const [fileToUpload, setFileToUpload] = useState(null);

        const [storeDetails, setStoreDetails] = useState({
                storeName: "MY STORE",
                urlRoute: "MY_STORE",
                storeDescription: "THE BEST STORE IN THE WORLD",
                address: "JERUSALEM",
                tel: "02:6277134",
                email: "MYSTORE@GMAIL.COM",
                colorDominates: "#F29544",
                policy: "",
                currency: "",
                logo: logoFurnitureShop,
                checkInventoryManagement: false,
                checkoneProductPurchase: false
        });
        function changeStoreDetails(event) {
                setAllStoreDetails(event.target.name, event.target.value)
        }
        function setAllStoreDetails(name, value) {
                setStoreDetails({
                        ...storeDetails,
                        [name]: value
                }
                )
        }
        function openMediaGallery() {
                props.setfunctionToSetImage("setLogoOnCreatingStore")
                props.setImageLocation(location.pathname)
                history.push("/" + props.objectFields.urlRoute + "/mediaGallery/uploudImage");
        }

        function convertSpacesToUnderlines(event) {
                let str = event.target.value
                let hasSpace = str.indexOf(' ');
                if (hasSpace > -1)
                        str = str.replace(/\s/g, '_')
                setAllStoreDetails("urlRoute", str)
        }
        const onSubmitStoreDetails = async (event) => {
                event.preventDefault()
                skip()
        }

        function skip() {
                props.setTitle(storeDetails.storeName)
                props.setAllOrders([])
                props.setCategories([])
                props.setAttribute([])
                props.setProducts([])
                props.createNewStore({ "store": storeDetails, "file": fileToUpload })
                history.push("/" + storeDetails.urlRoute);
        }
        return (
                <>
                        <Container fluid>
                                <Row md="12" className="OB-rowtop d-flex align-items-center justify-content-start">
                                        <Col md="1" className="pl-5 pr-0">
                                                <img src={logoApp}></img>
                                        </Col>
                                        <Col md="11" className="pl-0">Bullcommerce</Col>
                                </Row>



                                <Row md="12" className="OB-rowtop2">

                                        <img src={reka} className="OB-imgReka"></img>
                                        <div className="d-flex justify-content-center pt-4 OB-colOfTitle" style={{ position: 'absolute', textAlign: 'center', justifyContent: 'center', alignItems: 'center', left: '80vh' }}>Starting to create your own store<br></br>Update basic details about your store</div>
                                        {/* <div className="d-flex justify-content-center pt-4 OB-colOfTitle" style={{ position: 'absolute', textAlign: 'center', justifyContent: 'center', alignItems: 'center', left: '80vh' }}>Starting to create your own store<br></br>Update basic details about your store</div> */}


                                        {/* <Col className="d-flex justify-content-center">Update basic details about your store</Col> */}

                                        {/* <Col md="12" className="OB-title1 d-flex align-items-center">
                        Starting to create your own store</Col>
                    <Col md="12" className="OB-title2
                     d-flex align-items-center"
                    >Update basic details about your store</Col> */}
                                </Row>
                                <Row md="12" className="d-flex justify-content-center mt-5 mb-5">
                                        <Col md="7">
                                                <form onSubmit={onSubmitStoreDetails} md="12">
                                                        <Row className="d-flex justify-content-center OB-upLogo">Upload Logo</Row>
                                                        <Row className="row OB-uploadLogo mt-3"
                                                                onClick={(e) => {
                                                                        openMediaGallery()
                                                                }}>
                                                                <div className="OB-centerde pt-2"><img src={cloudGrey}></img><br></br>Click or drag image</div>
                                                        </Row>
                                                        <Row className="OB-lableForInput mt-4">Store Name</Row>
                                                        <Row><input className="OB-fillDetails"
                                                                name="storeName"
                                                                required
                                                                id="fname"
                                                                onBlur={convertSpacesToUnderlines}
                                                                onChange={changeStoreDetails}
                                                        >
                                                        </input></Row>

                                                        <Row className="OB-lableForInput">Address</Row>
                                                        <Row>
                                                                <input className="OB-fillDetails"
                                                                        required
                                                                        name="address"
                                                                        onChange={changeStoreDetails}
                                                                ></input></Row>

                                                        <Row className="OB-lableForInput">
                                                                <Col md="6" className="p-0">E-Mail</Col>
                                                                <Col md="6" className="p-0">Phone</Col>
                                                        </Row>
                                                        <Row>
                                                                <Col md="6" className="p-0 pr-4">
                                                                        <input className="OB-fillDetails"
                                                                                type={"email"}
                                                                                required
                                                                                name="email"
                                                                                onChange={changeStoreDetails}
                                                                        ></input>
                                                                </Col>
                                                                <Col md="6" className="p-0">
                                                                        <input className="OB-fillDetails"
                                                                                type={"tel"}
                                                                                name="tel"
                                                                                onChange={changeStoreDetails}
                                                                        ></input>
                                                                </Col>
                                                        </Row>

                                                        <Row className="OB-lableForInput">Select currency</Row>
                                                        <Row>
                                                                <Col md="9" className="p-0">
                                                                        <div className="OB-divOfSelect d-flex align-items-center">
                                                                                <select name="currency" id="cars"
                                                                                        onChange={changeStoreDetails}>
                                                                                        <option value="Israel ILS">Israel ILS</option>
                                                                                        <option value="Italy EUR">Italy EUR</option>
                                                                                        <option value="France EUR">France EUR</option>
                                                                                        <option value="Mexico MXN">Mexico MXN</option>
                                                                                </select>
                                                                        </div>

                                                                </Col>
                                                                <Col md="3" className="pl-4 d-flex align-items-center OB-mainColor">
                                                                        Main color
                                                                        <input className="ml-2 mb-2 OB-inputColor"
                                                                                type={"color"}
                                                                                name="colorDominates"
                                                                                onChange={changeStoreDetails}
                                                                        ></input>
                                                                </Col>
                                                        </Row>


                                                        <div className="d-flex justify-content-center mt-5">

                                                                <button type="submit" className="OB-createStore OB-add  ml-3">Create Your Store</button>

                                                        </div>
                                                        <br></br>
                                                        <Link className="d-flex justify-content-center" onClick={skip}>Skip Entering Details</Link>
                                                </form>
                                        </Col>
                                </Row>
                        </Container>
                </>
        )
}
const mapStateToProps = (state) => {
        return {

                coins: state.coinsReducer.coins,
                objectFields: state.storeReducer.objectFields,

        }
}
const mapDispatchToProps = (dispatch) => ({
        createNewStore: (objectFields) => dispatch(actions.createNewStore(objectFields)),
        // setSaveAllStoreDetails: (e) => dispatch(actions.setSaveAllStoreDetails(e)),
        setAllOrders: (e) => dispatch(actions.setAllOrders(e)),
        setCategories: (e) => dispatch(actions.setCategories(e)),
        setAttribute: (e) => dispatch(actions.setAttribute(e)),
        setProducts: (e) => dispatch(actions.setProducts(e)),
        setTitle: (e) => dispatch(actions.setTitle(e)),
        setfunctionToSetImage: (location) => dispatch(actions.setfunctionToSetImage(location)),
        setImageLocation: (location) => dispatch(actions.setImageLocation(location)),

})
export default connect(mapStateToProps, mapDispatchToProps)(OnBoarding);
