import React from 'react';
import { Button, Modal, ModalBody } from 'reactstrap';
import './modal.css';
import { connect } from 'react-redux';
import { actions } from '../../redux/action'

function ConfirmImgModal(props) {

    const {
        setOk,
        modal,
        setModal
    } = props;

    const toggle = () => setModal(!modal);

    return (
        <div>
            <Modal isOpen={modal} toggle={toggle}>
                <div className="delete_store_modal_header d-flex align-items-center">Upload Image</div>
                <ModalBody style={{ textAlign: 'center' }}>
                    <b>Sure you want to upload this image?</b><br />
                </ModalBody>
                <div className="d-flex justify-content-center">
                    <Button className="modal_cancel_button" onClick={toggle}>Cancel</Button>{' '}
                    <Button className="modal_delete_button" onClick={() => { setOk(true); toggle() }}>yes</Button>
                </div>
            </Modal>
        </div >
    );
}
const mapStateToProps = (state) => {
    return {
        imageLocation: state.uploadImageReducer.ImageLocation,
    }
}
const mapDispatchToProps = (dispatch) => ({

})
export default connect(mapStateToProps, mapDispatchToProps)(ConfirmImgModal)
