import produce from 'immer';
import createReducer from './reducerUtils';

const initialState = {
    hide:'true'
}

const hideConfiguratorReducer = {
    setHideConfigurator(state, action) {        
        state.hide = action.payload;
    }
}

export default produce((state, action) => createReducer(state, action, hideConfiguratorReducer), initialState);