export const getBullcommerceHeaderByStoreId = ({ dispatch, getState }) => next => action => {
        if (action.type === 'GET_BHD_BY_STORE_ID') {
                $.ajax({
                        url: `https://bullcommerce.shop/api/designs/getBHDByStoreId/${action.payload}`,
                        method: "get",
                        dataType: "json",
                        contentType: "application/json",
                        success: function (data) {
                                console.log("object design", data);
                                dispatch(actions.setBHD(data))
                        },
                        error: function (XMLHttpRequest, textStatus, errorThrown) {
                                console.log(XMLHttpRequest, " ", textStatus, " ", errorThrown)
                        }
                });
        };
        return next(action);
};


// /designs/editStoreNavbarDesign/:id
export const editStoreNavbarDesign = ({ dispatch, getState }) => next => action => {
        if (action.type === 'EDIT_STORE_NAVBAR_DESIGN') {
                return new Promise((resolve, reject) => {

                        var raw = JSON.stringify(getState().storeNavbarReducer.storeNavbarDesign);
                        $.ajax({
                                url: `https://bullcommerce.shop/api/designs/editStoreNavbarDesign/${getState().storeNavbarReducer.storeNavbarDesign._id}`,
                                method: "post",
                                dataType: "json",
                                contentType: "application/json",
                                data: raw,
                                success: function (data) {
                                        dispatch(actions.setStoreNavbar(data));
                                        resolve(data);
                                },
                                error: function (XMLHttpRequest, textStatus, errorThrown) {
                                        console.log(XMLHttpRequest, " ", textStatus, " ", errorThrown);
                                        reject(errorThrown);
                                }
                        });
                })
        };
        return next(action);
};

