import produce from 'immer';
import createReducer from '../reducerUtils';
import interior from "../../../assets/img/xd/interior-with-white-sofa@2x.png";
import room from "../../../assets/img/xd/room.jpg";
const initialState = {
        bullcommerceHeaderDesign: {},
        ImagesArr: [
                room,
                interior
        ],
        changeImgInCurrentLocation: -1
}


const bullcommerceHeader = {

        //BHD=bullcommerceHeaderDesign
        setBHD(state, action) {
                state.bullcommerceHeaderDesign = action.payload;
                // if (!state.bullcommerceHeaderDesign.sliderImages || state.bullcommerceHeaderDesign.sliderImages.length === 0)
                if (!state.bullcommerceHeaderDesign || state.bullcommerceHeaderDesign.sliderImages.length === 0)
                        state.bullcommerceHeaderDesign.sliderImages = state.ImagesArr;


        },
        setImagesArr(state, action) {
                state.bullcommerceHeaderDesign.sliderImages[state.changeImgInCurrentLocation] = action.payload;
                // if (state.changeImgInCurrentLocation == -1) {
                //         if (state.ImagesArr[0].src == "url(" + interior + ")")
                //                 state.ImagesArr = []
                //         state.ImagesArr.push({
                //                 src: "url(" + action.payload + ")",
                //         })
                // }
                // else {
                //         state.ImagesArr[state.changeImgInCurrentLocation].src = "url(" + action.payload + ")"

                // }
        },
        addPictureToSliderImages(state, action) {
                state.bullcommerceHeaderDesign.sliderImages.push(action.payload);
        },
        setBhTitle(state, action) {
                state.bullcommerceHeaderDesign.title.textContent = action.payload
        },
        setBhTitleSize(state, action) {
                state.bullcommerceHeaderDesign.title.textSize = action.payload
        },
        setBhTitleAlignment(state, action) {
                state.bullcommerceHeaderDesign.title.textAlignment = action.payload
        },
        setBhTitleColor(state, action) {
                state.bullcommerceHeaderDesign.title.textColor = action.payload
        },
        setBhTitlePaddingTop(state, action) {
                state.bullcommerceHeaderDesign.title.paddingTop = action.payload
        },
        setBhTitleDisplay(state, action) {
                state.bullcommerceHeaderDesign.title.display = !state.bullcommerceHeaderDesign.title.display
        },
        setChangeImgInCurrentLocation(state, action) {
                state.changeImgInCurrentLocation = action.payload;
        },
        removeOneImageFromSlider(state, action) {
                state.bullcommerceHeaderDesign.sliderImages = state.bullcommerceHeaderDesign.sliderImages.filter((currentImage, index) => {
                        if (index != action.payload)
                                return currentImage
                })
        }



}

export default produce((state, action) => createReducer(state, action, bullcommerceHeader), initialState);