import React, { useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { connect } from "react-redux";
import { actions } from "../../redux/action";
import "./ecommerceHeader.css"

function CategoryHeader(props) {
  const history = useHistory();
  const location = useLocation();
  function openMediaGallery() {
    props.setImageLocation(location.pathname)
    props.setfunctionToSetImage("setImageCategory")
    history.push("/" + props.objectFields.urlRoute + "/mediaGallery/uploudImage");
  }

  return (
    <div style={{ cursor: 'pointer' }}>
      {(props.category.currentCategory && props.category.currentCategory.categoryDesign.categoryTitle.display) &&
        <div className="category-header-title">
          <input className="bullcommerceTitleInput"

            value={props.category.currentCategory.categoryDesign.categoryTitle.textContent}
            onChange={(e) => { props.setCategoryTitle(e.target.value) }}
            style={{
              // textAlign: props.category.currentCategory.categoryDesign.categoryTitle.textAlignment ? props.category.currentCategory.categoryDesign.categoryTitle.textAlignment : 'center',
              textAlign: props.category.currentCategory.categoryDesign.categoryTitle.textAlignment ? props.category.currentCategory.categoryDesign.categoryTitle.textAlignment : 'center',
              fontSize: props.category.currentCategory.categoryDesign.categoryTitle.textSize ? props.category.currentCategory.categoryDesign.categoryTitle.textSize : "100px",
              color: props.category.currentCategory.categoryDesign.categoryTitle.textColor ? props.category.currentCategory.categoryDesign.categoryTitle.textColor : "white"

            }}
          ></input>
        </div>
      }
      <div onClick={() => openMediaGallery()}

        className="page-header header-filter carouelImgHover" style={{
          backgroundSize: '100% 100%', minHeight: '60vh',
          backgroundImage: 'url(' + props.category.currentCategory.categoryDesign.imageCategory + ')'
        }}>


      </div>
    </div>
  )
}
const mapStateToProps = (state) => {
  return {
    objectFields: state.storeReducer.objectFields,
    categoryTitle: state.categoryHeaderReducer.title,
    categoryImage: state.categoryHeaderReducer.image,
    alignment: state.bullPageEditReducer.alignment,
    ifDisplayTitle: state.bullPageEditReducer.ifDisplayTitle,
    category: state.categoriesReducer.currentCategory
  }
}
const mapDispatchToProps = (dispatch) => ({
  setCategoryTitle: (e) => dispatch(actions.setCategoryTitle(e)),
  changeCurrentComponent: (e) => dispatch(actions.setCurrentComponent(e)),
  setImageLocation: (e) => dispatch(actions.setImageLocation(e)),
  setfunctionToSetImage: (e) => dispatch(actions.setfunctionToSetImage(e))
})
export default connect(mapStateToProps, mapDispatchToProps)(CategoryHeader);