import produce from 'immer';
import createReducer from '../reducerUtils';


const initialState = {
        categories: [],
        stringCategory: "",
        currentCategory: {
                currentCategory: {},
                index: -1
        },
        categoryListMenu: [],
        ifNewCategory: false,
}

const category = {
        setCategories(state, action) {
                state.categories = action.payload;
                // state.categories =
                // let c = state.categories.map(x => {
                //         let strCategory = x.categoryName;
                //         let hasSpace = strCategory.indexOf(' ');
                //         if (hasSpace > -1)
                //                 strCategory = strCategory.replace(/\s/g, '_')
                //         // x.urlCategory = strCategory

                // })
                // console.log("cc", c);
                let sortP = state.categories;
                let bb2 = sortP.slice().sort((a, b) => {
                        let x = a.categoryDesign.indexInMenu;
                        let y = b.categoryDesign.indexInMenu;

                        if (x < y) { return -1; }
                        if (x > y) { return 1; }
                        return 0

                        // return a[columnName].localeCompare(b[columnName])
                });
                sortP = bb2;

                state.categoryListMenu = sortP;
                // state.currentCategory.currentCategory = action.payload[0];
        },
        setCategoryListMenu(state, action) {
                state.categoryListMenu = action.payload;
        },
        setCurrentCategory(state, action) {
                // state.currentCategory = action.payload;
                state.currentCategory.currentCategory = action.payload.currentCategory;
                if (action.payload.index > -1)
                        state.currentCategory.index = action.payload.index;
                // state.categories[state.currentCategory.index] = action.payload.currentCategory;
        },
        addNewCategory(state, action) {
                debugger
                state.categories.push(action.payload);
                state.categoryListMenu.push(action.payload)
        },
        setNewCategory(state, action) {
                debugger
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categories[c].categoryName = action.payload;
                c = state.categoryListMenu.indexOf(state.categoryListMenu.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categoryListMenu[c].categoryName = action.payload;
                state.currentCategory.currentCategory.categoryName = action.payload;
        },
        setImgNewCategory(state, action) {
                state.currentCategory.currentCategory.categoryDesign.imageCategory = action.payload;
        },
        setNewCategoryDesign(state, action) {
                state.currentCategory.currentCategory.categoryDesign.categoryTitle[action.payload.var] = action.payload.data;
        },
        deleteOldCategory(state, action) {
                debugger
                state.categories = state.categories.filter(x => x._id !== action.payload);
                state.categoryListMenu = state.categoryListMenu.filter(x => x._id !== action.payload);
        },
        // editOldCategory(state, action) {
        //         state.categories = state.categories.filter(x => x._id !== action.payload._id);
        //         state.categories.push(action.payload);
        // },
        setStatusShowCategory(state, action) {
                state.categories[action.payload].categoryDesign.statusShow = !state.categories[action.payload].categoryDesign.statusShow;
                // state.categoryListMenu = [];
                if (!state.categories[action.payload].categoryDesign.statusShow) {
                        const c = state.categoryListMenu.find(item => item._id === state.categories[action.payload]._id);
                        const index = state.categoryListMenu.indexOf(c);
                        if (index > -1) {
                                state.categoryListMenu.splice(index, 1);
                        }
                }
                else {
                        const c = state.categoryListMenu.find(item => item._id === state.categories[action.payload]._id);
                        const index = state.categoryListMenu.indexOf(c);
                        if (index === -1) {
                                state.categoryListMenu.push(state.categories[action.payload]);
                        }
                }
                // state.categoryListMenu.forEach(c => {
                //         debugger
                //         if (c._id === state.categories[action.payload]._id) {
                //                 if (state.categories[action.payload].categoryDesign.statusShow)
                //                         state.categoryListMenu.push(state.categories[action.payload]);
                //                 else {
                //                         const c = state.categoryListMenu.find(item => item._id === state.categories[action.payload]._id)
                //                         const index = state.categoryListMenu.indexOf(c);
                //                         if (index > -1) {
                //                                 state.categoryListMenu.splice(index, 1);
                //                         }
                //                 }
                //         }
                // });
        },
        setImageCategory(state, action) {
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categories[c].categoryDesign.imageCategory = action.payload;
                state.currentCategory.currentCategory.categoryDesign.imageCategory = action.payload;
        },
        setCategoryTitle(state, action) {
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categories[c].categoryDesign.categoryTitle.textContent = action.payload;
                state.currentCategory.currentCategory.categoryDesign.categoryTitle.textContent = action.payload;
        },
        setCategoryName(state, action) {
                if (state.categories[state.currentCategory.index])
                        state.categories[state.currentCategory.index].categoryName = action.payload;
                state.currentCategory.currentCategory = state.categories[state.currentCategory.index];
        },
        setAlignmentCategory(state, action) {
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categories[c].categoryDesign.categoryTitle.textAlignment = action.payload;
                state.currentCategory.currentCategory.categoryDesign.categoryTitle.textAlignment = action.payload;

        },
        setSizeCategory(state, action) {
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categories[c].categoryDesign.categoryTitle.textSize = action.payload;
                state.currentCategory.currentCategory.categoryDesign.categoryTitle.textSize = action.payload;
        },
        setColorTextCategory(state, action) {
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categories[c].categoryDesign.categoryTitle.textColor = action.payload;
                state.currentCategory.currentCategory.categoryDesign.categoryTitle.textColor = action.payload;
        },
        setDisplayTextCategory(state, action) {
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === state.currentCategory.currentCategory._id })[0])
                if (c > -1)
                        state.categories[c].categoryDesign.categoryTitle.display = !state.categories[c].categoryDesign.categoryTitle.display;
                state.currentCategory.currentCategory.categoryDesign.categoryTitle.display = !state.currentCategory.currentCategory.categoryDesign.categoryTitle.display
        },
        setIfNewCategory(state, action) {
                state.ifNewCategory = !state.ifNewCategory
        },
        editOneCategory(state, action) {
                let c = state.categories.indexOf(state.categories.filter(x => { return x._id === action.payload._id })[0]);
                if (c > -1) {
                        state.categories[c] = action.payload;
                }
        }



}
export default produce((state, action) => createReducer(state, action, category), initialState);