import React, { useState, useEffect } from 'react';
import './wrap-component.css'
import Content from './content';
import Configurator from './configurator';
import TopFrame from './topFrame';
import { connect, useSelector } from 'react-redux';
import { actions } from '../../redux/action';
import $ from 'jquery';
import Alert from '../alerts/notificationsAlert';

function Wrap(props) {
    // alert(props.showClientConfigurator);
    // const showClientConfigurator = useselector.
    const show = useSelector(state => state.productReducer.displayClientConfigurator)
    console.log("show?", show);
    const [flagCon, setFlagCon] = useState(true);
    const [flag, setFlag] = useState(true);
    useEffect(() => {
        let w = $("main").width();
        props.setMainWidth(w);
    }, [])
    return (
        <>
            <div style={{
                display: "flex",
                position: "relative"
            }}>
                <TopFrame setFlagCon={() => setFlagCon(!flagCon)}></TopFrame>
                <Configurator state={props} flag={show} setFlagCon={() => setFlagCon(show)} />
                {/* <Configurator state={props} flag={flagCon} setFlagCon={() => setFlagCon(!setFlagCon)} /> */}
                <main className="withRouter(Wrap)-content-13" style={{ paddingBottom: "0 !important", marginTop: "80px " }}>
                    <Content />
                </main>
                <Alert></Alert>
            </div >
        </>
    )
}
export default connect(
    (state) => {
        return {
            display: state.hideConfiguratorReducer.hide,
            // showClientConfigurator: state.productReducer.displatClientConfigurator,

        }
    },
    (dispatch) => {
        return {
            setMainWidth: (e) => dispatch(actions.setMainWidth(e))
        }
    }

)(Wrap);
