import { actions } from '../action';
import $ from 'jquery'

export const editCategoryDesign = ({ dispatch, getState }) => next => action => {
    if (action.type === 'EDIT_CATEGORY_DESIGN') {
        return new Promise((resolve, reject) => {

            var raw = JSON.stringify(action.payload);
            console.log("edit category design data", action.payload);
            $.ajax({
                url: `https://bullcommerce.shop/api/designs/editCategoryDesign/${action.payload._id}`,
                method: "post",
                dataType: "json",
                contentType: "application/json",
                data: raw,
                success: function (data) {
                    console.log("edit category design update", data);
                    resolve(data);
                },
                error: function (err) {
                    console.log("oww err")
                    reject(err);
                }
            });
        })
    }
    return next(action);
}











