import axios from 'axios';
import $ from 'jquery';
import { actions } from '../action';


export const getAllCategories = ({ dispatch, getState }) => next => action => {
    if (action.type === 'GET_ALL_CATEGORIES') {
        axios.get('https://bullcommerce.shop/api/categories')
            .then(res => {
                dispatch(actions.setCategories(res.data))
            }).catch(err => console.log("errrrrrrr", err));
    }
    return next(action);
};


export const createNewCategory = ({ dispatch, getState }) => next => action => {
    return new Promise((resolve, reject) => {
        if (action.type === 'CREATE_NEW_CATEGORY') {
            dispatch(actions.setIfNewCategory());
            let category = getState().categoriesReducer.currentCategory.currentCategory;
            var raw = JSON.stringify({
                categoryName: category.categoryName,
                categoryDescription: category.categoryDescription,
                image: category.image,
                store: category.store,
                color: category.color,
                // masterCategory,
                // slug,
                // childrenCategory,
                categoryImage: category.categoryDesign.imageCategory,
                textContent: category.categoryDesign.categoryTitle.textContent,
                textSize: category.categoryDesign.categoryTitle.textSize,
                textAlignment: category.categoryDesign.categoryTitle.textAlignment,
                display: category.categoryDesign.categoryTitle.display,
                textColor: category.categoryDesign.categoryTitle.textColor,
                paddingTop: category.categoryDesign.categoryTitle.paddingTop,
                statusShow: category.categoryDesign.statusShow,
                indexInMenu: category.categoryDesign.indexInMenu
            });
            $.ajax({
                url: "https://bullcommerce.shop/api/categories/newCategoty",
                method: "post",
                dataType: "json",
                contentType: "application/json",
                data: raw,
                success: function (data) {
                    console.log("new category", data);
                    dispatch(actions.addNewCategory(data));
                    resolve(data)
                    // alert("הקטגוריה נוצרה בהצלחה")
                },
                error: function (err) {
                    console.log(err)
                    reject(err)
                    alert("Failed to add category")
                }
            });
        }
        return next(action);
    })
};

export const deleteCategory = ({ dispatch, getState }) => next => action => {
    if (action.type === 'DELETE_CATEGORY') {
        axios.post('https://bullcommerce.shop/api/categories/deleteCategoty/' + action.payload)
            .then(res => {
                dispatch(actions.deleteOldCategory(action.payload))
                dispatch(actions.setAlertVisible());
                dispatch(actions.setAlertContent("The category deleted successfully."));
                dispatch(actions.setAlertColor("success"));


            }).catch(() => {
                console.log("error");
                dispatch(actions.setAlertVisible());
                dispatch(actions.setAlertContent("The category deletion failed."));
                dispatch(actions.setAlertColor("danger"));
            })
    }
    return next(action);
};
export const editCategory = ({ dispatch, getState }) => next => action => {
    if (action.type === 'EDIT_CATEGORY') {
        var raw = JSON.stringify(
            action.payload
            // { categoryName: action.payload.categoryName, masterCategory: action.payload.masterCategory, color: action.payload.color }
        );
        $.ajax({
            url: `https://bullcommerce.shop/api/categories/editCategoty/${action.payload._id}`,
            method: "post",
            dataType: "json",
            contentType: "application/json",
            data: raw,
            success: function (data) {
                // dispatch(actions.editOldCategory(data))
                // alert("The category updated successfully!")
                dispatch(actions.setAlertVisible());
                dispatch(actions.setAlertContent("The category updated successfully."));
                dispatch(actions.setAlertColor("green"));
            },
            error: function (XMLHttpRequest, textStatus, errorThrown) {
                console.log(XMLHttpRequest, " ", textStatus, " ", errorThrown)
                alert("The category editing failed")

            }
        });
    };
    return next(action);
};
export const getCategoriesByStore = ({ dispatch, getState }) => next => action => {
    if (action.type === 'GET_CATEGORIES_BY_STORE') {
        console.log("I'm in getCategoriesByStore");
        axios.get('https://bullcommerce.shop/api/stores/storeCategories/' + action.payload)
            .then(res => {
                console.log(" succes getCategoriesByStore", res);
                dispatch(actions.setCategories(res.data))
                let options = Object.assign({}, res.data[0].products[0], { "color": res.data[0].color });
                let list = [];
                res.data.forEach(c => {
                    c.products.forEach(p => {
                        let options = Object.assign({}, p, { "color": c.color, "categoryName": c.categoryName });
                        list.push(options);
                    })
                });
                dispatch(actions.setProducts(list))
                dispatch(actions.setFilteredItems(list));
            })
            .catch(err => console.log("errrrrrrr", err));
    }
    return next(action);
}
export const setNewCategoryInCurrentCategory = ({ dispatch, getState }) => next => action => {
    if (action.type === 'SET_NEW_CATEGORY_IN_CURRENT_CATEGORY') {
        dispatch(actions.setCurrentCategory({
            currentCategory: {
                categoryName: "NEW CATEGORY",
                color: Math.floor(Math.random() * 16777215).toString(16),
                store: getState().storeReducer.objectFields._id,
                categoryDescription: "The new category, please enter content",
                categoryDesign: {
                    statusShow: true,
                    imageCategory: "https://files.codes/uploads/Yeudit/img/1620036106063__437.jpg",
                    categoryTitle: {
                        display: true,
                        textColor: "white",
                        textContent: "NEW CATEGORY",
                        textSize: "74px",
                        textAlignment: "center",

                    },
                    indexInMenu: getState().categoriesReducer.categories.length
                }
                , index: -1
            }
        }))
    }
    return next(action);
}