import { actions } from '../action';
import axios from 'axios';

export const newOrder = ({ dispatch, getState }) => next => action => {
    if (action.type === 'NEW_ORDER') {
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");
        var raw = JSON.stringify({ "trackingID": 1, "user": action.payload.user, "store": action.payload.store, "userAddress": action.payload.address, "date": action.payload.date, "status": action.payload.status, "products": action.payload.product, "totalPrice": action.payload.totalPrice });
        var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
        };
        fetch("https://bullcommerce.shop/api/orders/newOrder", requestOptions)
            .then(createOrder => {
                // alert("הזמנתך נקלטה במערכת")
                dispatch(actions.setAlertVisible());
                dispatch(actions.setAlertContent("The store has been successfully updated."));
                dispatch(actions.setAlertColor("primary"));

            })
            .catch(error => console.log('error', error));
    }
    return next(action);
};

export const getOrdersByStore = ({ dispatch, getState }) => next => action => {
    if (action.type === 'GET_ORDERS_BY_STORE') {

        axios.get('https://bullcommerce.shop/api/stores/storeOrders/' + getState().storeReducer.objectFields._id)
            .then(res => {
                dispatch(actions.setAllOrders(res.data))
            })
            .catch(err => console.log("errrrrrrr", err));
    }
    return next(action);
}

export const deleteOrder = ({ dispatch, getState }) => next => action => {
    if (action.type === 'DELETE_ORDER') {
        axios.post('https://bullcommerce.shop/api/orders/deleteOrder/' + action.payload)
            .then(res => {
                dispatch(actions.setDeleteOrder(action.payload))
                // alert("ההזמנה נמחק בהצלחה")
            }).catch(err => console.log("errrrrrrr", err));
    }
    return next(action);
};
