import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import './modal.css';
import { connect } from 'react-redux';
import { actions } from '../../redux/action'

function deleteStoreModal(props) {

    const {
        storeName,
        modal,
        setModal,
        store
    } = props;

    const toggle = () => setModal(!modal);

    function deleteSto() {
        props.deleteStore(store._id);
    }

    return (
        <div>
            <Modal isOpen={modal} toggle={toggle}>
                <div className="delete_store_modal_header d-flex align-items-center">Delete your Store</div>
                <ModalBody style={{ textAlign: 'center' }}>
                    <b>Sure you want to move {storeName} to trash?</b><br />
                                        Once you delete your store becomes unpublished and can't be adited.
                </ModalBody>
                <div className="d-flex justify-content-center">
                    <Button className="modal_cancel_button" onClick={toggle}>Cancel</Button>{' '}
                    <Button className="modal_delete_button" onClick={() => { deleteSto(); toggle() }}>Delete</Button>
                </div>
            </Modal>
        </div >
    );
}
const mapStateToProps = (state) => {
    return {
    }
}
const mapDispatchToProps = (dispatch) => ({
    deleteStore: (i) => { dispatch(actions.deleteStore(i)) },

})
export default connect(mapStateToProps, mapDispatchToProps)(deleteStoreModal)
