import produce from 'immer';
import createReducer from '../reducerUtils';
import interior from "../../../assets/img/xd/interior-with-white-sofa@2x.png";
import room from "../../../assets/img/xd/room.jpg";
const initialState = {
        storeNavbarDesign: {},
}


const storeNavbar = {

        setStoreNavbar(state, action) {
                state.storeNavbarDesign = action.payload;
        },
        setNavbarBackgroundColor(state, action) {
debugger
                state.storeNavbarDesign.backgroundColor = action.payload;
        },
        // textColor: { type: String },
        setNavbarTextColor(state, action) {
                state.storeNavbarDesign.textColor = action.payload;
        },
        // activeTextColor:
        setNavbarActiveTextColor(state, action) {
                state.storeNavbarDesign.activeTextColor = action.payload;
        }

}

export default produce((state, action) => createReducer(state, action, storeNavbar), initialState);