import produce from 'immer';
import createReducer from "../reducerUtils";

const initialState = {
        displayClientConfigurator: true,
        displayManagerConfigurator: true,
        newProduct: {
                name: "",
                description: "",
                amount: 1,
                SKU: 0,
                categoryName: "",
                category: '',
                store: '',
                price: 0,
                image: '',
                // colors: [],
                attributes: [],
                featured: '',
                salePrice: 0,
                photoGallery: '',
                video: '',
                isStock: true,
                isDraft: true,
                weight: 0,
        },
        editProduct: {
                name: "",
                description: "",
                amount: 1,
                SKU: 0,
                categoryName: "",
                category: '',
                store: '',
                price: 0,
                image: '',
                // colors: [],
                attributes: '',
                featured: '',
                sale_price: 0,
                photoGallery: '',
                video: '',
                isStock: true,
                isDraft: true,
                weight: 0,
        },
        locationToReturn: "",
        // newProduct: {},

        leavedPage: true,

        products: [],
        // featuredProducts: [],
        pic: "",
        viewListOrGrid: "list",
        currentProduct: {
                currentProduct: {},
                index: -1
        },
        treeProduct: new Array(4)
};

const product = {
        setLocationToReturn(state, action) {
                state.locationToReturn = action.payload;
        },
        addImageToProduct(state, action) {
                state.newProduct.image = action.payload;
        },
        changeImageToProduct(state, action) {
                state.currentProduct.currentProduct.image = action.payload;
        },
        // update the new product fields
        setNewProduct(state, action) {
                state.newProduct[action.payload.var] = action.payload.data;
        },
        setEditProduct(state, action) {
                debugger;
                state.currentProduct.currentProduct[action.payload.var] = action.payload.data;
        },
        addAttributeToCurrentProduct(state, action) {
                debugger;
                let newColorsArray = [...state.currentProduct.currentProduct.attributes];
        },
        setLeavedPage(state, action) {
                debugger;
                state.leavedPage = action.payload;
        },
        clearNewProductData(state, action) {
                state.newProduct.image = "";
                state.newProduct.price = 0;
                state.newProduct.salePrice = 0;
                state.newProduct.name = "";
                state.newProduct.SKU = 0;
                state.newProduct.description = "";
                state.newProduct.isStock = false;
                state.newProduct.isDraft = false;
                state.newProduct.weight = 0;
                state.newProduct.amount = 0;
        },
        // update the edit product fields
        // add the chose url image to new product

        // add the chose url image to edit product
        // addImageToProduct(state, action) {
        //         state.editProduct.image = (action.payload);
        // },
        showImageToProduct(state, action) {
                state.newProduct.image = (action.payload);
        },

        // addNewProduct(state, action) {
        //         debugger;
        //         console.log(action.payload);
        //         // state.newProduct.name = (action.payload.name);
        // },

        setCurrentProduct(state, action) {
                state.currentProduct.currentProduct = action.payload.currentProduct;
                if (action.payload.index > -1)
                        state.currentProduct.index = action.payload.index;
                // state.products[action.payload.index] = state.currentProduct;
        },
        addToTreeProduct(state, action) {
                state.treeProduct[0] = state.treeProduct[1];
                state.treeProduct[1] = state.treeProduct[2];
                state.treeProduct[2] = state.treeProduct[3]
                state.treeProduct[3] = action.payload;
        },
        setProducts(state, action) {
                state.products = action.payload;
                // state.featuredProducts = state.products.filter(x => x.featured === true)
        },
        // setProductImage(state, action) {

        //         state.pic = action.payload.p
        //         state.products[action.payload.i].image = action.payload.p
        //         // state.products[action.payload.i].images[0] = action.payload.p
        //         // state.pic = state.products[action.payload.i].images[0]

        // },
        setLOrG(state, action) {
                state.viewListOrGrid = action.payload
        },
        // setCurrentProduct(state, action) {

        //         state.currentProduct = action.payload
        // },

        addNewProduct(state, action) {
                debugger;
                state.products.push(action.payload);
                // if (action.payload.featured === true)
                //         state.featuredProducts.push(action.payload)
        },
        deleteOldProduct(state, action) {
                state.products = state.products.filter(x => x._id !== action.payload);
        },
        editOldProduct(state, action) {
                state.products = state.products.filter(x => x._id !== action.payload._id);

                state.products.push(action.payload);
        },
        setPriceProduct(state, action) {
                state.currentProduct.currentProduct.price = action.payload;
                state.products[state.currentProduct.index].price = action.payload
        },
        setSalePrice(state, action) {
                state.currentProduct.currentProduct.salePrice = action.payload
                state.products[state.currentProduct.index].salePrice = action.payload
        },
        setWeight(state, action) {
                state.currentProduct.currentProduct.weight = action.payload
                state.products[state.currentProduct.index].weight = action.payload
        },
        setIsStock(state, action) {
                state.currentProduct.currentProduct.isStock = !state.currentProduct.currentProduct.isStock;
                state.products[state.currentProduct.index].isStock = !state.currentProduct.currentProduct.isStock
        },
        setIsFeatured(state, action) {
                state.currentProduct.currentProduct.featured = !state.currentProduct.currentProduct.featured
                state.products[state.currentProduct.index].featured = !state.currentProduct.currentProduct.featured
        },
        setIsDraft(state, action) {
                state.currentProduct.currentProduct.isDraft = !state.currentProduct.currentProduct.isDraft
                state.products[state.currentProduct.index].isDraft = !state.currentProduct.currentProduct.isDraft;

        },
        setDisplayClientConfigurator(state, action) {
                state.displayClientConfigurator = action.payload;
        },
        setDisplayManagerConfigurator(state, action) {
                state.displayManagerConfigurator = action.payload;
        }
};

export default produce((state, action) => createReducer(state, action, product), initialState);