import React, { useEffect } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import './wrap-component.css'
import AdminCurd from '../store setting/admin'
import Dnd from '../navbars/DndCategories'
import Cart from '../store design/cart_page/cart';
import { useCookies } from "react-cookie";
import { connect } from 'react-redux';
import MainStoreRoutes from "../store design/store_page/mainStore"
import TopFooter from "../topFooter"
import MediaGallery from '../store design/media_gallery/mediaGallery';
import { actions } from '../../redux/action';
import Product from '../store design/product_page/product';
import crudProducts from '../store setting/product_management/crudProducts';
import crudCategory from '../store setting/category_managment/crudCategory';
import crudOrder from '../store setting/crudOrder';
import storeSettingsManagement from '../store setting/storeSettingsManagement';
import storePerUser from '../store setting/storePerUser';
import createNewPaper from '../store setting/paper_managment/createNewPaper';
import CrudAttributes from '../store setting/attribute_management/crudAttribute';
// import newProduct from '../store setting/product_management/newProduct';
import editProductNew from '../store setting/product_management/editProductNew';
import ProductNew from '../store setting/product_management/productNew';
import { swap } from 'formik';
import newProduct from '../store setting/product_management/newProduct';
import ScrollToTop from './scrollToTop';
import { Fragment } from 'react';
function Content(props) {
    const [cookies, setCookie, removeCookies] = useCookies(["order"]);

    useEffect(() => {
        get();

    }, [])

    function get() {
        let t = cookies.order;
        if (t != undefined)
            props.setCart(t)
        else
            props.setCart({ products: [], totalPrice: 0 })
    }
    window.addEventListener("beforeunload", (ev) => {
        ev.preventDefault();
        setCookie("order", props.cart, {
            path: "/"
        });
    });
    return (
        <>
            <div className="Content">
                <ScrollToTop />

                {/* <Router>
                    <Fragment> */}
                <Switch>
                    <Route path="/CrudCategory" component={crudCategory} />
                    <Route path="/crudProducts" component={crudProducts} />
                    <Route path="/CrudOrder" component={crudOrder} />
                    <Route path="/StoreSettingsManagement" component={storeSettingsManagement} />
                    <Route path="/StorePerUser" component={storePerUser} />
                    <Route path="/CrudAttributes" component={CrudAttributes} />
                    <Route path="/NewPaper" component={createNewPaper} />
                    <Route path="/dnd" component={Dnd} />
                    <Route path="/:storeName/mediaGallery" component={MediaGallery} />
                    <Route path="/:storeName/admin" component={AdminCurd} />
                    <Route path="/newProduct" component={newProduct} />
                    <Route path="/editProduct" component={editProductNew} />
                    <Route path="/ProductNew" component={ProductNew} />
                    <MainStoreRoutes></MainStoreRoutes>
                </Switch>
                {/* </Fragment>
                </Router> */}

            </div>

        </>
    )
}


const mapStateToProps = (state) => {
    return {
        storeCurrent: state.storeReducer.objectFields._id,
        cart: state.cartReducer.cart
    }
}
const mapDispatchToProps = (dispatch) => ({
    setCart: (e) => { dispatch(actions.setOrder(e)) },

})
export default connect(mapStateToProps, mapDispatchToProps)
    (Content);
