import produce from 'immer';
import createReducer from './reducerUtils';

const initialState = {
    title: 'Uploud Images',
    myFiles: [{}],
    trash: [{}]
}

const filter = {

    setTitleBySideBar(state, action) {
        state.title = action.payload;
    },
    setMyFiles(state, action) {
        state.myFiles = JSON.parse(action.payload);
    },
    setTrash(state, action) {
        state.trash = action.payload;
    },
    setDeleteImage(state, action) {
        state.myFiles = state.myFiles.filter(x => x._id != action.payload._id);
    }
}

export default produce((state, action) => createReducer(state, action, filter), initialState);