import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { actions } from '../../redux/action';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem, NavItem, NavLink } from 'reactstrap';
import { Link, useLocation, useHistory, NavLink as RRNavLink } from "react-router-dom";

import "./scrollNavbar.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function Dnd(props) {
        const history = useHistory();
        const { activeHome, setActiveHome, activeCategory, setActiveCategory } = props;
        useEffect(() => {
                props.setNewCategoryInCurrentCategory()
        }, [])
        const onDragEnd = (result) => {
                if (!result.destination) {
                        return;
                }
                const items = reorder(
                        props.categories.filter(item => {
                                if ((!item.masterCategory || item.masterCategory === null)
                                        // && item.categoryDesign && item.categoryDesign.statusShow === true
                                )
                                        return item
                        }),
                        result.source.index,
                        result.destination.index
                );
                props.setListMenu(items)
        }
        const reorder = (list, startIndex, endIndex) => {
                const result = [list.length]
                for (let index = 0; index < list.length; index++) {
                        result[index] = list[index];

                }
                const [removed] = result.splice(startIndex, 1);
                result.splice(endIndex, 0, removed);
                return result;
        };
        const getItemStyle = (isDragging, draggableStyle) => ({
                userSelect: 'none',
                paddingRight: '5px',
                paddingLeft: '5px',
                fontSize: '15px',
                height: '2rem',
                ...draggableStyle,

        });

        const getListStyle = isDraggingOver => ({
                display: 'flex',
                padding: '10',
                overflowX: 'scroll',
                // marginLeft: '80px',שמתי בהערה כי נראה מ יותר ומעוות את השורה
                enabled: 'false',
                // width: '100%'
        });
        // function convertSpacesToUnderlines(event) {
        //         let strCategory = event
        //         let hasSpace = strCategory.indexOf(' ');
        //         if (hasSpace > -1)
        //                 strCategory = strCategory.replace(/\s/g, '_')
        //         props.setStringCategory(strCategory)
        // }

        return (
                <>

                        <DragDropContext onDragEnd={onDragEnd}>
                                <Droppable droppableId="droppable" direction="horizontal">
                                        {(provided, snapshot) => (
                                                <div
                                                        ref={provided.innerRef}
                                                        style={getListStyle(snapshot.isDraggingOver)}
                                                        {...provided.droppableProps}

                                                >
                                                        {/* <Draggable
                                                        key={0} draggableId={0} index={0}>
                                                        {(provided, snapshot) => (
                                                                <div
                                                                        className="drag_category"
                                                                        id={0}
                                                                        ref={provided.innerRef}
                                                                        {...provided.draggableProps}
                                                                        {...provided.dragHandleProps}
                                                                        style={getItemStyle(
                                                                                snapshot.isDragging,
                                                                                provided.draggableProps.style,

                                                                        )}
                                                                >
                                                                        {/* arrows-alt */}
                                                        {/* <FontAwesomeIcon className="drag_icon_category mr-2" icon={['fas', 'grip-vertical']}></FontAwesomeIcon>
                                                                        <Link to={`/${props.storeName}/`}>
                                                                                Home
                                                                        </Link>                                                                        </div>
                                                        )}
                                                </Draggable> */}

                                                        {props.categories.filter(item => {
                                                                if (!item.masterCategory && item.masterCategory === null
                                                                        // && item.categoryDesign && item.categoryDesign.statusShow === true
                                                                )
                                                                        return item
                                                        }).map((item, index) => (

                                                                <Draggable
                                                                        onChange={() => { props.setCategoryTitle(item.categoryName) }}
                                                                        key={index} draggableId={`${index}`} index={index}>
                                                                        {(provided, snapshot) => (
                                                                                <div
                                                                                        className="drag_category d-flex justify-content-center"
                                                                                        id={index}
                                                                                        ref={provided.innerRef}
                                                                                        {...provided.draggableProps}
                                                                                        {...provided.dragHandleProps}
                                                                                        style={getItemStyle(
                                                                                                snapshot.isDragging,
                                                                                                provided.draggableProps.style,
                                                                                        )}
                                                                                >
                                                                                        {/* arrows-alt */}
                                                                                        {/* <FontAwesomeIcon className="drag_icon_category mr-2" icon={['fas', 'grip-vertical']}></FontAwesomeIcon> */}

                                                                                        {/* <Link to={`/${props.storeName}/category/${props.stringCategory}`} 
                                                                                                 <Link to={`/${props.storeName}/category/${item.categoryName}`}
                                                                                                         style={{ color: (activeCategory === item.categoryName ? props.activeTextColor : props.textColor), backgroundColor: "transparent" }}
                                                                                                         onClick={(e) => {
                                                                                //                                 e.stopPropagation()
                                                                                //                                 // convertSpacesToUnderlines(item.categoryName)
                                                                                //                                 props.setcomponnet("CategoryEdit");
                                                                                //                                 props.setCurrentCategory({ currentCategory: item, index: index });
                                                                                //                                 // convertSpacesToUnderlines(item.categoryName)‏


                                                                                //                         }}>
                                                                                //                         {item.categoryName}
                                                                                //                 </Link>*/}


                                                                                        <NavItem
                                                                                                className="d-flex justify-content-center"
                                                                                        // id={index}
                                                                                        // ref={provided.innerRef}
                                                                                        // {...provided.draggableProps}
                                                                                        // {...provided.dragHandleProps}
                                                                                        // style={getItemStyle(
                                                                                        //         snapshot.isDragging,
                                                                                        //         provided.draggableProps.style,
                                                                                        // )}
                                                                                        >
                                                                                                {/* arrows-alt */}
                                                                                                <FontAwesomeIcon className="drag_icon_category" icon={['fas', 'grip-vertical']}></FontAwesomeIcon>

                                                                                                {/* <Link to={`/${props.storeName}/category/${props.stringCategory}`} */}
                                                                                                <NavLink tag={RRNavLink} to={`/${props.storeName}/category/${item.categoryName}`}
                                                                                                        style={{ color: (activeCategory === item.categoryName ? props.activeTextColor : props.textColor) }}
                                                                                                        onClick={(e) => {
                                                                                                                e.stopPropagation()
                                                                                                                // convertSpacesToUnderlines(item.categoryName)
                                                                                                                props.setcomponnet("CategoryEdit");
                                                                                                                props.setCurrentCategory({ currentCategory: item, index: index });
                                                                                                                // convertSpacesToUnderlines(item.categoryName)‏


                                                                                                        }}
                                                                                                >
                                                                                                        {item.categoryName}
                                                                                                </NavLink>
                                                                                        </NavItem>
                                                                                </div>

                                                                        )}
                                                                </Draggable>
                                                        ))}
                                                        {provided.placeholder}

                                                        {props.isAdmin &&
                                                                
                                                                <NavItem
                                                                // id={index}
                                                                // ref={provided.innerRef}
                                                                // {...provided.draggableProps}
                                                                // {...provided.dragHandleProps}
                                                                // style={getItemStyle(
                                                                //         snapshot.isDragging,
                                                                //         provided.draggableProps.style,
                                                                // )}
                                                                >
                                                                        {/* arrows-alt */}

                                                                        {/* <Link to={`/${props.storeName}/category/${props.stringCategory}`} */}
                                                                        <NavLink id="dnd-newCategory" className="pt-0" tag={RRNavLink} to={"/" + props.objectFields.urlRoute + "/category/New"}
                                                                                onClick={(e) => {
                                                                                        // e.preventDefault()
                                                                                        e.stopPropagation()
                                                                                        // props.setNewCategory(newCategoryObj)
                                                                                                                             // props.setcomponnet("EditAttribute");
                                                                                        props.setcomponnet("NewCategoryConfigurator");
                                                                                        props.setIfNewCategory()
                                                                                        // history.push("/" + props.objectFields.urlRoute + "/category/New");
                                                                                }}
                                                                                onM
                                                                        >
                                                                                <FontAwesomeIcon style={{ color: props.objectFields.colorDominates }} className="pt-0" icon={['fas', 'plus']}></FontAwesomeIcon>
                                                                        </NavLink>
                                                                </NavItem>

                                                                // <Link to={"/" + props.objectFields.urlRoute + "/addCategory"}
                                                                // <Link to={"/" + props.objectFields.urlRoute + "/category/New"}
                                                                //         onClick={(e) => {
                                                                //                 // e.preventDefault()
                                                                //                 e.stopPropagation()
                                                                //                 // props.setNewCategory(newCategoryObj)
                                                                //                 props.setCurrentCategory({ currentCategory: newCategoryObj, index: -1 })
                                                                //                 // props.setcomponnet("EditAttribute");
                                                                //                 props.setcomponnet("NewCategoryConfigurator");
                                                                //                 props.setIfNewCategory()
                                                                //                 // history.push("/" + props.objectFields.urlRoute + "/category/New");
                                                                //         }
                                                                //         }>
                                                                //         <span className="badge rounded-pill badge-notification" style={{ backgroundColor: "#FC894D" }}>+</span>
                                                                // </Link>
                                                        }




                                                </div>

                                        )}
                                </Droppable>
                        </DragDropContext>
                </>
        );
}
export default connect(
        (state) => {
                return {
                        stringCategory: state.categoriesReducer.stringCategory,
                        // ObjCategory: state.createCategoryReducer.ObjCreateCategory,
                        categories: state.categoriesReducer.categoryListMenu,
                        storeName: state.storeReducer.objectFields.urlRoute ?
                                state.storeReducer.objectFields.urlRoute :
                                state.storeReducer.objectFields.storeName,
                        objectFields: state.storeReducer.objectFields,
                        category: state.categoriesReducer.currentCategory,
                        isAdmin: state.viewOrEditReducer.isAdmin,
                        textColor: state.storeNavbarReducer.storeNavbarDesign.textColor,
                        activeTextColor: state.storeNavbarReducer.storeNavbarDesign.activeTextColor
                        // postData: state.createPostReducer.postData
                }
        },
        (dispatch) => {
                return {
                        setListMenu: (listMenu) => { dispatch(actions.setCategoryListMenu(listMenu)) },
                        setNewCategoryInCurrentCategory: () => { dispatch(actions.setNewCategoryInCurrentCategory()) },
                        setCategoryTitle: (title) => { dispatch(actions.setCategoryTitle(title)) },

                        // setFilterPosts: (value) => { dispatch(setFilterPosts(value)) },
                        setcomponnet: (r) => dispatch(actions.setCurrentComponent(r)),
                        setCurrentCategory: (c) => dispatch(actions.setCurrentCategory(c)),
                        // setNewCategory: (c) => dispatch(actions.setNewCategory(c)),
                        setStringCategory: (strCategory) => dispatch(actions.setStringCategory(strCategory)),
                        setIfNewCategory: () => dispatch(actions.setIfNewCategory())
                }
        }
)(Dnd)
