import { combineReducers } from 'redux';

import productReducer from './data_reducer/productReducer';
import categoriesReducer from './data_reducer/categoryReducer';
import cartReducer from './cartReducer';
import ordersReducer from './data_reducer/ordersReducer';
import storeHomeReducer from "./edit_reducer/old.storeHomePageReducer";
import viewOrEditReducer from "./edit_reducer/viewOrEditReducer";
import storeReducer from "./storeReducer";
import userReducer from "./userReducer";
import coinsReducer from "./coinsReducer";
import filterReducer from "./filterReducer";
import wrapReducer from "./wrapReducer";
import bullPageEditReducer from "./edit_reducer/bullPageEditReducer";
import attributeReducer from "./data_reducer/attributeReducer";
import quillReducer from './data_reducer/quillReducer';
import mediaGalleryReducer from './mediaGalleryReducer'
import uploadImageReducer from './uploadImageReducer'
import hideConfiguratorReducer from './hideConfiguratorReducer'
import BHD from './design_reducers/bullcommerceHeaderReducer';
import categoryHeaderReducer from './design_reducers/categoryHeaderReducer';
import storeNavbarReducer from './design_reducers/storeNavbarReducer';
import newsletterReducer from './design_reducers/newsletterReducer';
import alertReducer from './design_reducers/alertReducer';

const appReducers = combineReducers({
        ordersReducer,
        cartReducer,
        categoriesReducer,
        productReducer,
        userReducer,
        viewOrEditReducer,
        storeHomeReducer,
        coinsReducer,
        storeReducer,
        wrapReducer,
        filterReducer,
        quillReducer,
        attributeReducer,
        bullPageEditReducer,
        mediaGalleryReducer,
        uploadImageReducer,
        BHD,
        categoryHeaderReducer,
        hideConfiguratorReducer,
        storeNavbarReducer,
        newsletterReducer,
        alertReducer
});

export default appReducers;