import produce from 'immer';
import createReducer from '../reducerUtils';

const initialState = {
        visible: false,
        alertContent: "notification",
        alertColor: "info"
}

const alert = {
        setAlertVisible(state, action) {
                state.visible = !state.visible;
        },
        setAlertContent(state, action) {
                state.alertContent = action.payload;
        },
        setAlertColor(state, action) {
                state.alertColor = action.payload;
        }
}

export default produce((state, action) => createReducer(state, action, alert), initialState);