export const getStoreByUser = ({ dispatch, getState }) => next => action => {
    if (action.type === 'GET_STORE_BY_USER') {

        axios.get('https://bullcommerce.shop/api/users/getAllStores/' + action.payload)
            .then(res => {
                dispatch(actions.setStorePerUser(res.data))
            })
            .catch(err => console.log("errrrrrrr", err));
    }
    return next(action);
}
export const deleteStore = ({ dispatch, getState }) => next => action => {
    if (action.type === 'DELETE_STORE') {
        axios.post('https://bullcommerce.shop/api/stores/deleteStore/' + action.payload)
            .then(res => {
                dispatch(actions.deleteOldStore(action.payload))
            });
    }


    return next(action);
};

export const editStore = ({ dispatch, getState }) => next => action => {

    if (action.type === 'EDIT_STORE') {
        return new Promise((resolve, reject) => {

            var raw = JSON.stringify(getState().storeReducer.objectFields);
            console.log("editStore", JSON.stringify(getState().storeReducer.objectFields));



            $.ajax({
                url: `https://bullcommerce.shop/api/stores/editStore/${getState().storeReducer.objectFields._id}`,
                method: "post",
                dataType: "json",
                contentType: "application/json",
                data: raw,
                success: function (data) {
                    console.log("data edit store", data);
                    dispatch(actions.setSaveAllStoreDetails(data));
                    dispatch(actions.getOrdersByStore(data._id));
                    dispatch(actions.getCategoriesByStore(data._id));
                    dispatch(actions.getAllPaper(data._id));
                    dispatch(actions.getAllAttributes(data._id));
                    resolve(data);
                },


                error: function (err) {
                    console.log("update store is failed :-(", err);
                    reject(err);
                }
            })
        })
    }
    return next(action);
};

export const saveStore = ({ dispatch, getState }) => next => action => {

    if (action.type === 'SAVE_STORE') {

        // let p1 = dispatch(actions.editBullcommerceHeaderDesign());
        // let p2 = dispatch(actions.editStore());
        // let p3 = dispatch(actions.editStoreNavbarDesign());

        // Promise.all([p1, p2, p3

        dispatch(actions.editBullcommerceHeaderDesign());
        dispatch(actions.editStore());
        dispatch(actions.editStoreNavbarDesign());
        getState().categoriesReducer.categories.forEach(category => {
            let categoryInMenu = getState().categoriesReducer.categoryListMenu.find(x => x._id === category._id);
            const index = getState().categoriesReducer.categoryListMenu.indexOf(categoryInMenu);
            // let c;
            if (index > -1) {
                dispatch(actions.editOneCategory({ ...category, categoryDesign: { ...category.categoryDesign, indexInMenu: index } }))
                // c = { ...category.categoryDesign, indexInMenu: index }
            }
            // else
            //     c = category.categoryDesign;
            // dispatch(actions.editCategoryDesign(c))
            dispatch(actions.editCategory(category));
        });
        // dispatch(actions.editStoreNavbarDesign()) למה נמחק?

        dispatch(actions.setAlertVisible());
        dispatch(actions.setAlertContent("The store has been successfully updated."));
        dispatch(actions.setAlertColor("primary"));
        if (getState().categoriesReducer.ifNewCategory)
            dispatch(actions.createNewCategory(getState().categoriesReducer.currentCategory.currentCategory))
        dispatch(actions.addNewProducts(getState().productReducer.newProduct))
        dispatch(actions.editProduct(getState().productReducer.currentProduct.currentProduct));
        dispatch(actions.editNewsletterDesign());
        // dispatch(actions.clearNewProductData())
    }
    return next(action);
};
