import React, { useEffect, useState } from "react";

import { Route, Redirect, useHistory } from "react-router-dom";
// import { AuthContext } from "./Auth";
import { connect } from 'react-redux';
import { actions } from './redux/action'

const PrivateRoute = ({ component: RouteComponent, ...rest }) => {
    // let userName = rest.computedMatch.params.userName;//לקחת מהקוקיס

    //userName from cookies:
    let userName = document.cookie && document.cookie.includes("userName") ? document.cookie.split(";")
        .filter(s => s.includes('userName'))[0].split("=").pop() : null;

    //jwt from cookies:
    let TokenToString = document.cookie && document.cookie.includes("jwt") ? document.cookie.split(";")
        .filter(s => s.includes('jwt'))[0].split("=").pop() : null;
    // if (!userName || !TokenToString)
    //     redirectToLogin()
    const [isLoading, setIsLoading] = useState(true);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const history = useHistory();

    let routes = rest.computedMatch.params.nameVideo;
    useEffect(() => {
        const isLocal = window.location.hostname == "localhost"
        const url = `https://bullcommerce.shop/${userName}/isPermission?isLocal=${isLocal}`;
        // const url = `https://bullcommerce.shop/${"Rut%20Kramer"}/isPermission?isLocal=${isLocal}`;
        console.log(userName, " -userName .", TokenToString, " -jwt")
        const isPermission = async () => {
            let response = await fetch(url, {
                method: 'GET',
                headers: {
                    'Authorization': TokenToString,//tokenToString
                    // 'Authorization': "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1aWQiOiJjaldKdVZZNEhoZERLTjNoV0RmZWdJZGhwVDczIiwiZW1haWwiOiJydXRAbGVhZGVyLmNvZGVzIiwiaXAiOiIyMTIuMTk5LjkyLjI1MCIsImlhdCI6MTYxOTMzNzU5MH0.2EwRo5Oah8r4VI82tJMtLXT5M-x9EQZuyoi9rjMap6E",//tokenToString
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
            }).then(response => {
                debugger
                if (response.status == 401) {
                    setIsLoading(false)
                    setIsLoggedIn(true)
                }
                else {
                    response.json()
                        .then(user => {

                            history.push("/")
                            rest.setUser(user)
                            rest.setTokenFromCookies(TokenToString)
                            debugger; console.log(user)
                            // rest.funcReset()

                            setIsLoading(false)
                            // setUser({ "_id": user._id, "username": user.username, "email": user.email, "tokenFromCookies": TokenToString })
                            // dispatch(actions.setId(data.uid));
                            // dispatch(actions.setUser({ "uid": data.uid, "username": data.username, "email": data.email, "tokenFromCookies": noQuotesJwtData }))
                        })

                    //למלא פה את היוזר
                };
                // response.json()})
                // .then(user =>{debugger;console.log(user)
                // }
                //  ) 
                //  setUser(user)) 
                // if (response.status == 401) {
                //     setIsLoading(false)
                //     setIsLoggedIn(true)
                // }
                // else {
                //     setIsLoading(false)
                //     // response.json()
                //     //למלא פה את היוזר
                // }
            })
        }
        isPermission()
    }, [])

    return isLoading ?
        <div className="page-header header-filter" filter-color="grey">
            <div className="content" style={{ margin: "30vh" }}>
                <h1><strong> ...loading</strong></h1>
            </div>
        </div>
        : isLoggedIn ?
            // "" :
            redirectToLogin(routes) :
            <Route
                {...rest}
                render={routeProps =>
                    <RouteComponent {...routeProps} />
                }
            />
        // <Redirect to={"/"} />

        ;
};
export default
    connect((state) => {
        return {
            // user: state.userReducer.user
        }
    },
        (dispatch) => {
            return {
                setUser: (e) => dispatch(actions.setUser(e)),
                setTokenFromCookies: (e) => dispatch(actions.setTokenFromCookies(e)),
                funcReset: () => dispatch(actions.funcReset())
            }
        })(PrivateRoute)

function redirectToLogin(routes) {
    window.location.href = routes ?
        `https://dev.accounts.codes/bullcommerce/login?routes=${routes}` :
        `https://dev.accounts.codes/bullcommerce/login`;
    return null
}


